import { forwardRef, useImperativeHandle, useState } from 'react'
import PropTypes from 'prop-types'

// komponentin luova funktio on "kritty" funktiokutsun forwardRef sislle, eli komponentti psee
// ksiksi sille mriteltyyn refiin
const Togglable = forwardRef((props, ref) => {
  const [visible, setVisible] = useState(false)

  const hideWhenVisible = { display: visible ? 'none' : '' }
  const showWhenVisible = { display: visible ? '' : 'none' }

  const toggleVisibility = () => {
    setVisible(!visible)
  }

  // React hook jonka avulla komponentille (Togglable) voidaan mritt
  // funktioita joita voidaan kutsua sen ulkopuolelta, eli nyt kun luodaan uusi blogi niin voidaan
  // kutsua tt
  useImperativeHandle(ref, () => {
    return {
      toggleVisibility
    }
  })

  return (
    <div>
      <div style={hideWhenVisible}>
        <button onClick={toggleVisibility}>{props.buttonLabel}</button>
      </div>
      <div style={showWhenVisible}>
        {props.children}
        <button onClick={toggleVisibility}>cancel</button>
      </div>
    </div>
  )
})

Togglable.displayName = 'Togglable'
// buttonLabel mritelln pakolliseksi String-tyyppiseksi propsiksi
Togglable.propTypes = {
  buttonLabel: PropTypes.string.isRequired
}

export default Togglable